<template>
  <div class="personal-main">
    <personal-header></personal-header>
    <div class="personal-content" ref="content">
      <div>
        <!-- 功能列表 -->
        <div class="menu-top">
          <cell class="cell" :path="val.path" v-for="(val, index) in menuTopList" :key="index" :icon="val.icon" :cellTitle="val.title">
          </cell>
        </div>
        <!-- 帮助，账号设置 -->
        <div class="menu-bottom">
          <cell class="cell" :path="val.path" v-for="(val, index) in menuBottomList" :key="index" :icon="val.icon" :cellTitle="val.title">
          </cell>
        </div>
        <!-- 需要资质认证显现 -->
        <div class="certification" v-if="isShowCertification" @click="_toApply">
          <div class="bg"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/**
 * @author lastbee@163.com
 * @timer [2018/12/25 10:03]
 * @desc 个人中心功能
 * @desc 展示用户的支持功能，名字，头像，称号以及评分
 */
import personalHeader from '@/components/personal/personalHeader'
import cell from '@/components/personal/cell'
import BScroll from 'better-scroll'
import { mapGetters } from 'vuex'
export default {
  components: {
    'personal-header': personalHeader,
    cell
  },
  data() {
    return {
      scroll: '', // scroll对象 用于控制滚动区域
      isShowCertification: false,// 是否展示资质认证
      menuTopList: [{
        icon: 'icon-gongzuozheng',
        title: '我的工作证',
        path: '/login'
      },{
        icon: 'icon-qianbao',
        title: '我的钱包',
        path: '/login'
      },{
        icon: 'icon-pingfenshezhi',
        title: '我的评分'
      },{
        icon: 'icon-paiming',
        title: '我的排名',
        path: '/'
      },{
        icon: 'icon-renwu',
        title: '我的任务'
      },{
        icon: 'icon-tuandui',
        title: '我的团队',
        path: '/'
      }],
      menuBottomList: [{
        icon: 'icon-gongzuozheng',
        title: '帮助反馈',
        path: '/'
      },
      {
        icon: 'icon-shezhi',
        title: '账号设置',
        path: '/login'
      }]
    }
  },
  computed: {
    ...mapGetters(['get_userInfo'])
  },
  mounted() {
    this._handleUserInfo() // 处理用户信息
    setTimeout(() => {
      this.scroll = new BScroll(this.$refs.content,{
        probeType: 1,
        click: true,
        eventPassthrough: 'horizontal'
      })
    },20)
  },
  methods: {
    _handleUserInfo() { // handele userInfo
      if ( this.get_userInfo ) {
        this.isShowCertification = this.get_userInfo.isShowCertification
        this.imageId = this.get_userInfo.imageId
      } else {
        return
      }
    },
    _toApply() { // 去申请资质认证
      this.$router.push('/qualification')
    }
  },
}
</script>

<style lang="scss" scoped>
@import "../../css/mixin.scss";
.personal-main{
  position: fixed;
  top:0;
  left: 0;
  width:100%;
  height:100%;
  z-index:1;
  .personal-content {
    z-index:5;
    height: calc(100% - 44.3vw + 10px);
    min-height: calc(100% - 174px);
    position: relative;
    top:-10px;
    border-radius: 10px;
    overflow: hidden;
    background-color: rgb(245, 245, 245);
    .menu-top {
      background-color: rgb(255, 255, 255);
    }
    .menu-bottom {
      background-color: rgb(255, 255, 255);
      margin-top: 10px;
    }
    .certification{
      padding:0 15px;
      .bg{
        width:100%;
        height: 23.8vw;
        max-height: 152px;
        @include bg-image('../../assets/imgs/zizhirenzheng');
        background-size: 100% 100%;
        margin-top: 10px;
      }
    }
    .cell {
      position: relative;
      &:after {
        position: absolute;
        content: ' ';
        left: 15px;
        right: 15px;
        bottom: 0px;
        height: 1px;
        border: none;
        background:rgb(204,204,204);
        @media (-webkit-min-device-pixel-ratio: 3),(min-device-pixel-ratio: 3) {
          transform: scaleY(0.333)
        }
        @media (-webkit-min-device-pixel-ratio: 2),(min-device-pixel-ratio: 2) {
          transform: scaleY(.5)
        }
      }
      &:last-child::after{
        display:none
      }
    }
  }
}

</style>

